const thottles=function(){
    this.dingshi='';
};
thottles.prototype.timeEnd =function (fn,time){
    if(this.dingshi){
        this.dingshi=clearTimeout(this.dingshi)
    }
    this.dingshi=setTimeout(function () {
        fn()
    },time);
};
const thottles_news=new thottles();

